import './Menu.scss';
import manuIcon from './../../assets/images/menu.svg';
// import userAvatar from './../../assets/images/user.jpg';
import userAvatar from '../../assets/images/user.png';
import Divider from '@mui/material/Divider';
import { NavLink } from 'react-router-dom';

const Menu = ({
    currentLanguage,
    darkMode
}) => {

    return (
        <nav className={!darkMode ? 'menu' : 'menu menu-dark'}>
            <a className='menu__avatar' href="#"><img src={userAvatar} alt=""/></a>
            
            {/* <button><img src={manuIcon} width={32} alt="" /></button> */}
            <ul>
                <NavLink className={!darkMode ? 'menu__link' : 'menu__link menu__link-dark'} to={'/home'}>
                    {currentLanguage == 'en' ? 'Home' : ''}
                </NavLink>
                <NavLink className={!darkMode ? 'menu__link' : 'menu__link menu__link-dark'} to={'/stats'}>
                    {currentLanguage == 'en' ? 'Stats' : ''}
                </NavLink>
                <NavLink className={!darkMode ? 'menu__link' : 'menu__link menu__link-dark'} to={'/settings'}> 
                    {currentLanguage == 'en' ? 'Settings' : ''}
                </NavLink>
            </ul>

        </nav>
    )
}

export default Menu;