import './GreetingsBlock.scss';

const GreetingsBlock = ({ currentLanguage }) => {

    let date = new Date();
    let weekDay = date.getDay();
    let day = date.getDate();
    let month = date.getMonth();
    let year = date.getFullYear();
    let hours = date.getHours();
    let timeOfDay;
    let greetingsWords;


    if (hours >= 5 && hours <= 11) {
        if (currentLanguage == 'en') {
            timeOfDay = 'morning';
        } else {
            timeOfDay = '';
            greetingsWords = '';
        }
    } else if (hours > 11 && hours <= 17) {
        if (currentLanguage == 'en') {
            timeOfDay = 'afternoon';
        } else {
            timeOfDay = ''
            greetingsWords = '';
        }
    } else if (hours > 17 && hours <= 22) {
        if (currentLanguage == 'en') {
            timeOfDay = 'evening';
        } else {
            timeOfDay = ''
            greetingsWords = '';
        }
    } else if (hours == 23 || hours == 0 || hours == 1 || hours == 2 || hours == 3 || hours == 4) {
        if (currentLanguage == 'en') {
            timeOfDay = 'night';
        } else {
            timeOfDay = ''
            greetingsWords = '';
        }
    }

    switch (day) {
        case 0:
            currentLanguage == 'en' ? weekDay = 'Sunday' : weekDay = '';
            break;
        case 1:
            currentLanguage == 'en' ? weekDay = 'Monday' : weekDay = '';
            break;
        case 2:
            currentLanguage == 'en' ? weekDay = 'Tuesday' : weekDay = '';
            break;
        case 3:
            currentLanguage == 'en' ? weekDay = 'Wednesday' : weekDay = '';
            break;
        case 4:
            currentLanguage == 'en' ? weekDay = 'Thursday' : weekDay = '';
            break;
        case 5:
            currentLanguage == 'en' ? weekDay = 'Friday' : weekDay = '';
            break;
        case 6:
            currentLanguage == 'en' ? weekDay = 'Saturday' : weekDay = '';
            break;
        default:
            weekDay = ''
            break;
    }

    return (
        <div className="greetingsBlock">
            <h1>{currentLanguage == 'en'
                ? 'Good'
                : greetingsWords} {timeOfDay}, {currentLanguage == 'en' ? 'User' : ''}!
            </h1>
            <div className='greetingsBlock__weekDay'>{weekDay}</div>
            <div className='greetingsBlock__date'>{day}/{month + 1}/{year}</div>
        </div>
    )
}

export default GreetingsBlock;